.app-wrapper {
    display: grid;
    grid-template-areas:
    'h h h'
    'n c c'
    'f f f';
    grid-template-rows: 40px 1fr;
    grid-template-columns: 2fr 5fr 5fr;
    /*grid-gap: 10px;*/
    width: 1200px;
    margin: 0 auto;
    font: 16px "Comic Sans MS", cursive;
    height: 100vh;
}

.app-wrapper-content {
    grid-area: c;
    background: #dbbdb3;
}